<template>
  <el-card shadow="never" style="margin: 15px">
    <span slot="header">{{ name }}</span>
    <draggable
      v-model="dataList"
      class="list"
      tag="div"
      :group="{ name: 'dragobj', pull: 'clone', put: false }"
      :clone="cloneData"
      @start="drag = true"
      @end="drag = false"
    >
      <div
        v-for="item in dataList"
        :key="item.id"
        class="item"
        :class="{ active: item.show }"
        :title="item.title"
      >
        <span class="arrow" @click="item.show = !item.show"
          ><i class="el-icon-arrow-right"></i
        ></span>
        <div class="head"><i class="el-icon-folder"></i> {{ item.title }}</div>
        <el-collapse-transition>
          <ul v-show="item.show" class="sublist">
            <li v-for="(i, n) in item.fields" :key="n">
              <i class="el-icon-document"></i> {{ i.label }}
            </li>
          </ul>
        </el-collapse-transition>
      </div>
    </draggable>
  </el-card>
</template>
<script>
import draggable from "vuedraggable";
import { v4 as uuidv4 } from "uuid";

export default {
  name: "JSource",
  components: {
    draggable,
  },
  props: {
    name: {
      type: String,
      default: "",
    },
    data: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      filter: "",
      dataList: [],
    };
  },
  methods: {
    cloneData(item) {
      //转成字符串，实现深拷贝
      const data = JSON.parse(JSON.stringify(item));
      data.id = uuidv4();
      return data;
    },
  },
  mounted() {
    this.dataList = this.data;
  },
};
</script>
<style>
.list > .item {
  position: relative;
  cursor: move;
  padding: 6px 2px;
}
.list > .item:hover {
  background-color: #f9f9f9;
}
.list > .item > .head {
  margin: 6px 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.list > .item > .arrow {
  float: right;
  transform: rotate(0);
  transition: all 0.2s;
  cursor: pointer;
  padding: 4px 8px;
}
.list > .item.active > .arrow {
  transform: rotate(90deg);
  transition: all 0.2s;
}
.list > .item::after {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #ddd;
  content: "";
}
.list > .item .sublist {
  list-style: none;
  margin: 0;
  padding: 0 0 0 0.4em;
}
.list > .item .sublist li {
  padding: 3px 0;
}
.list > .item i {
  color: #bbb;
}
</style>
