<template>
  <el-container class="j-builder">
    <el-main class="position-relative p-0">
      <el-container class="h-100">
        <el-main
          v-for="(union, index) in unionList"
          :key="index"
          v-show="currentIndex === index"
        >
          <transition-group
            name="el-zoom-in-center"
            class="table-wrap"
            tag="div"
          >
            <div
              v-for="line in union.lineList"
              :key="line.id"
              class="line"
              :style="{
                '--before-height': line.style.leftHeight + 'px',
                '--after-height': line.style.rightHeight + 'px',
                left: line.style.left + 'px',
                top: line.style.top + 'px',
                width: line.style.width + 'px',
              }"
            >
              <el-dropdown
                placement="bottom-start"
                :show-timeout="300"
                :hide-timeout="200"
                @command="lineSetting($event, line)"
              >
                <i
                  class="el-icon-connection"
                  :class="{ active: activeId === line.id }"
                  @click="joinLine('line', line.id)"
                ></i>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    icon="el-icon-edit-outline"
                    command="setting"
                    >设置</el-dropdown-item
                  >
                  <el-dropdown-item icon="el-icon-delete" command="delete"
                    >删除</el-dropdown-item
                  >
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </transition-group>
          <draggable
            v-model="union.tableList"
            tag="div"
            class="draggable"
            group="dragobj"
            :handle="'.el-card__header'"
            @start="drag = true"
            @end="drag = false"
            @change="changeList"
          >
            <transition-group
              name="el-zoom-in-center"
              class="table-wrap"
              tag="div"
            >
              <div
                :ref="table.id"
                class="table"
                v-for="table in union.tableList"
                :key="table.id"
              >
                <div
                  class="dot"
                  :class="{ active: activeId === table.id }"
                  @click="joinLine('table', table.id)"
                ></div>
                <el-card shadow="never">
                  <div slot="header" class="clearfix">
                    <div style="line-height: 130%">
                      {{ table.title }}<br />
                      <small style="color: #999">{{ table.code }}</small>
                    </div>
                    <el-popover width="160" v-model="table.showConfirm">
                      <p>确定删除该子数据集吗？</p>
                      <div style="text-align: right; margin: 0">
                        <el-button
                          size="mini"
                          type="text"
                          @click="table.showConfirm = false"
                          >取消</el-button
                        >
                        <el-button
                          type="primary"
                          size="mini"
                          @click="deleteTable(table)"
                          >确定</el-button
                        >
                      </div>
                      <el-button
                        slot="reference"
                        style="position: absolute; right: -8px; top: -8px"
                        type="default"
                        plain
                        size="mini"
                        icon="el-icon-close"
                        circle
                      ></el-button>
                    </el-popover>
                  </div>
                  <div>{{ table.text }}</div>
                  <el-table
                    ref="table"
                    :data="table.fields"
                    size="small"
                    :show-header="false"
                    :row-class-name="tableRowClassName"
                    @row-click="clickRow(table, $event)"
                  >
                    <el-table-column width="40" label="选择">
                      <template slot-scope="{ row }">
                        <el-checkbox
                          v-model="row.select"
                          :key="row.value"
                          :disabled="row.select"
                          active-color="#409EFF"
                          inactive-color="#999"
                          @change="toList(table, row)"
                        />
                      </template>
                    </el-table-column>
                    <el-table-column prop="label" label="字段名" />
                  </el-table>
                </el-card>
              </div>
            </transition-group>
          </draggable>
          <el-empty
            v-if="!unionList[currentIndex].tableList.length"
            description="请将左侧的数据拖入这里"
          ></el-empty>
        </el-main>
        <el-footer height="120px" class="footer">111</el-footer>
      </el-container>
    </el-main>
    <el-aside width="360px">
      <div class="pannel">
        <el-form
          v-if="fieldForm.label"
          :model="fieldForm"
          size="mini"
          label-position="right"
        >
          <el-divider
            v-if="unionList[currentIndex].fieldList.length"
            content-position="left"
            class="mt-5 mb-3"
            >{{ fieldForm.tableTitle }}</el-divider
          >
          <el-form-item label="字段名" class="mb-2">
            <el-input v-model="fieldForm.label" disabled></el-input>
          </el-form-item>
          <el-form-item label="别名" class="mb-2">
            <el-input v-model="fieldForm.name"></el-input>
          </el-form-item>
          <el-form-item
            v-if="fieldForm.type === 'string'"
            label="字符串格式"
            class="mb-2"
          >
            <el-select v-model="fieldForm.stringType" placeholder="字符串格式">
              <el-option label="Count" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            v-if="fieldForm.type === 'date'"
            label="日期格式"
            class="mb-2"
          >
            <el-select v-model="fieldForm.dateType" placeholder="日期格式">
              <el-option label="YYYY" value="1"></el-option>
              <el-option label="MM" value="2"></el-option>
              <el-option label="YYYY-MM-DD" value="3"></el-option>
              <el-option label="YYYY-MM-DD " value="4"></el-option>
              <el-option label="YYYY-MM-dd HH:mm:ss" value="5"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item v-if="fieldForm.type === 'number'" label="数字聚合">
            <el-select v-model="fieldForm.numberType" placeholder="数字聚合">
              <el-option label="Max" value="1"></el-option>
              <el-option label="Min" value="2"></el-option>
              <el-option label="Avg" value="3"></el-option>
              <el-option label="Sum" value="4"></el-option>
              <el-option label="Count" value="5"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="saveFieldForm">保存</el-button>
          </el-form-item>
        </el-form>
        <el-divider
          v-if="unionList[currentIndex].fieldList.length"
          content-position="left"
          class="mt-5 mb-3"
          >列表</el-divider
        >
        <draggable
          v-model="unionList[currentIndex].fieldList"
          class="list"
          tag="div"
          :group="{ name: 'dragobj', pull: 'clone', put: false }"
          @start="drag = true"
          @end="drag = false"
        >
          <div
            v-for="(item, index) in unionList[currentIndex].fieldList"
            :key="item.id"
            class="item"
            :class="{ active: item.show }"
            :title="item.title"
          >
            <span class="arrow" @click="item.show = !item.show"
              ><i class="el-icon-close" @click="toTable(item, index)"></i
            ></span>
            <div class="head">
              <i class="el-icon-document"></i
              ><strong>{{ item.tableTitle }}</strong> {{ item.label }}
            </div>
          </div>
        </draggable>
      </div>
      <div class="j-union">
        <el-select
          v-model="unionType"
          size="small"
          placeholder="Union类型"
          clearable
          style="width: 100%; margin-bottom: 20px"
        >
          <el-option
            v-for="item in unionOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
        <div class="union-list">
          <div
            v-for="(item, index) in unionList"
            :key="index"
            class="item"
            :class="{ active: index === currentIndex }"
            @click="changeSubDataSet(index)"
          >
            <el-popover
              v-if="unionList && unionList.length > 1"
              placement="left"
              width="160"
              v-model="item.showConfirm"
            >
              <p>确定删除该子数据集吗？</p>
              <div style="text-align: right; margin: 0">
                <el-button
                  size="mini"
                  type="text"
                  @click="item.showConfirm = false"
                  >取消</el-button
                >
                <el-button
                  type="primary"
                  size="mini"
                  @click="deleteSubDataSet(index)"
                  >确定</el-button
                >
              </div>
              <el-button
                slot="reference"
                type="default"
                plain
                size="mini"
                icon="el-icon-close"
                circle
              ></el-button>
            </el-popover>
            <span>子数据集 #{{ index + 1 }}</span>
          </div>
          <i class="el-icon-connection"></i>
        </div>
        <div class="item btn" @click="addSubDataSet()">
          <i class="el-icon-plus"></i>
        </div>
      </div>
    </el-aside>
    <j-setting
      ref="setting"
      :start-options="startOptions"
      :end-options="endOptions"
      @save="saveSetting"
    />
  </el-container>
</template>
<script>
import draggable from "vuedraggable";
import { v4 as uuidv4 } from "uuid";
import JSetting from "./j-setting";

const point = { type: "", id: "" };

export default {
  name: "JBuilder",
  components: {
    draggable,
    JSetting,
  },
  data() {
    return {
      activeId: "",
      addLine: { id: "", startPoint: { ...point }, endPoint: { ...point } },
      activeRow: {},
      fieldForm: {},
      startOptions: [],
      endOptions: [],
      // dimensionOptions: [
      //   { label: '维度', value: '1' },
      //   { label: '统计值', value: '2' }
      // ],
      // fieldTypeOptions: [
      //   { label: '字符串', value: '1' },
      //   { label: '数字', value: '2' },
      //   { label: '日期', value: '3' }
      // ],
      unionList: [{ index: 0, tableList: [], lineList: [], fieldList: [] }],
      unionType: "",
      currentIndex: 0,
      unionOptions: [
        { label: "UNION", value: "1" },
        { label: "UNION ALL", value: "2" },
      ],
    };
  },
  methods: {
    resetData() {
      this.addLine = {
        id: "",
        startPoint: {
          ...point,
        },
        endPoint: {
          ...point,
        },
      };
      this.activeId = "";
    },
    async createLine() {
      if (!this.unionList[this.currentIndex].lineList.length) {
        return;
      }
      this.unionList[this.currentIndex].lineList.forEach((line) => {
        const style = this.genarateLine(line.startPoint, line.endPoint);
        this.$set(line, "style", style);
      });
      this.resetData();
    },
    genarateLine(startPoint, endPoint) {
      let top = 0;
      let startLeft = 0;
      let startTop = 0;
      let endLeft = 0;
      let endTop = 0;
      const height = 16;
      if (startPoint.type === "table") {
        this.unionList[this.currentIndex].tableList.forEach((table) => {
          if (table.id === startPoint.id) {
            const dom = this.$refs[table.id][0];
            startLeft = dom.offsetLeft + dom.clientWidth / 2;
            if (!top || top >= dom.offsetTop) {
              top = dom.offsetTop - height - 4;
            }
            if (startTop < dom.offsetTop) {
              startTop = dom.offsetTop;
            }
          }
        });
      } else if (startPoint.type === "line") {
        this.unionList[this.currentIndex].lineList.forEach((line) => {
          if (line.id === startPoint.id) {
            const style = line.style;
            startLeft = style.left + style.width / 2;
            if (!top || top >= style.top) {
              top = style.top - height;
            }
            if (startTop < style.top) {
              startTop = style.top;
            }
          }
        });
      }
      if (endPoint.type === "table") {
        this.unionList[this.currentIndex].tableList.forEach((table) => {
          if (table.id === endPoint.id) {
            const dom = this.$refs[table.id][0];
            endLeft = dom.offsetLeft + dom.clientWidth / 2;
            if (!top || top >= dom.offsetTop) {
              top = dom.offsetTop - height - 4;
            }
            if (endTop < dom.offsetTop) {
              endTop = dom.offsetTop;
            }
          }
        });
      } else if (endPoint.type === "line") {
        this.unionList[this.currentIndex].lineList.forEach((line) => {
          if (line.id === endPoint.id) {
            const style = line.style;
            endLeft = style.left + style.width / 2;
            if (!top || top >= style.top) {
              top = style.top - height;
            }
            if (endTop < style.top) {
              endTop = style.top;
            }
          }
        });
      }
      if (startLeft < endLeft) {
        return {
          left: startLeft,
          top: top,
          width: endLeft - startLeft,
          leftHeight: startTop - top,
          rightHeight: endTop - top,
        };
      } else {
        return {
          left: endLeft,
          top: top,
          width: startLeft - endLeft,
          leftHeight: endTop - top,
          rightHeight: startTop - top,
        };
      }
    },
    joinLine(type, id) {
      // 控制是否高亮、连线
      if (type === "table") {
        let disabled = this.unionList[this.currentIndex].lineList.find(
          (i) => i.startPoint.id === id || i.endPoint.id === id
        );
        if (disabled) {
          return;
        }

        if (this.activeId === id) {
          this.resetData();
          return;
        } else {
          this.activeId = id;
        }
      } else {
        let disabled = this.unionList[this.currentIndex].lineList.find(
          (i) => i.startPoint.id === id || i.endPoint.id === id
        );
        if (disabled) {
          console.log("该点不允许");
          return;
        }

        if (this.activeId === id) {
          this.resetData();
          return;
        } else {
          this.activeId = id;
        }
      }

      // 创建线
      if (!this.addLine.startPoint.id) {
        //创建第一点
        this.addLine.startPoint.id = id;
        this.addLine.startPoint.type = type;
      } else {
        // 创建第二点
        if (this.addLine.startPoint.id === id) {
          // 如果重复
          this.addLine.startPoint = {
            ...point,
          };
          return;
        } else {
          //第二点
          this.addLine.endPoint.id = id;
          this.addLine.endPoint.type = type;
          this.addLine.id = uuidv4();
          this.unionList[this.currentIndex].lineList.push(this.addLine);
          this.createLine();
        }
      }
    },
    getOptions(point) {
      let resultArr = [];
      const returnOptions = (point) => {
        if (point.type === "table") {
          const table = this.unionList[this.currentIndex].tableList.find(
            (table) => table.id === point.id
          );
          let fields = table.fields.filter((o) => !o.select);
          resultArr.push({ label: table.title, fields: fields });
        } else {
          this.unionList[this.currentIndex].lineList.forEach((line) => {
            if (line.id === point.id) {
              returnOptions(line.startPoint);
              returnOptions(line.endPoint);
            }
          });
        }
      };
      returnOptions(point);
      return resultArr;
    },
    lineSetting(command, line) {
      if (command === "setting") {
        this.startOptions = this.getOptions(line.startPoint, false);
        this.endOptions = this.getOptions(line.endPoint, false);
        this.$refs.setting.init(line.id, line.setting);
      } else if (command === "delete") {
        this.deleteLine(line.id);
        this.resetData();
      }
    },
    saveSetting(id, form) {
      this.unionList[this.currentIndex].lineList.forEach((line) => {
        if (line.id === id) {
          line.setting = form;
        }
      });
    },
    deleteLine(id) {
      const index = this.unionList[this.currentIndex].lineList.findIndex(
        (line) => line.id === id
      );
      this.unionList[this.currentIndex].lineList.splice(index, 1);
      this.createLine();

      this.unionList[this.currentIndex].lineList.forEach((line) => {
        if (line.startPoint.id === id || line.endPoint.id === id) {
          this.deleteLine(line.id);
        }
      });
    },
    deleteTable(table) {
      // 删除表
      table.showConfirm = false;
      const index = this.unionList[this.currentIndex].tableList.findIndex(
        (item) => item.id === table.id
      );
      this.unionList[this.currentIndex].tableList.splice(index, 1);
      // 删除线
      this.unionList[this.currentIndex].lineList.forEach((line) => {
        if (
          (line.startPoint.type === "table" &&
            line.startPoint.id === table.id) ||
          (line.endPoint.type === "table" && line.endPoint.id === table.id)
        ) {
          this.deleteLine(line.id);
        }
      });
    },
    changeList() {
      this.$nextTick(() => {
        this.createLine();
      });
    },
    toList(table, row) {
      const item = Object.assign(
        { tableTitle: table.title, tableId: table.id },
        row
      );
      // console.log(item)
      this.unionList[this.currentIndex].fieldList.push(item);
    },
    toTable(data, index) {
      this.unionList[this.currentIndex].tableList.forEach((table) => {
        table.fields.forEach((i) => {
          if (i.id === data.id) {
            i.select = false;
          }
        });
      });
      this.unionList[this.currentIndex].fieldList.splice(index, 1);
    },
    clickRow(table, row) {
      if (this.activeRow !== row) {
        this.activeRow = row;
        this.fieldForm = Object.assign(
          { tableTitle: table.title, tableId: table.id },
          row
        );
      } else {
        this.activeRow = {};
        this.fieldForm = {};
      }
    },
    tableRowClassName({ row }) {
      if (row === this.activeRow) {
        return "active-row";
      } else {
        return "";
      }
    },
    saveFieldForm() {
      this.unionList[this.currentIndex].tableList.forEach((table) => {
        if (table.id === this.fieldForm.tableId) {
          table.fields.forEach((i) => {
            if (i.id === this.fieldForm.id) {
              i.name = this.fieldForm.name;
              if (this.fieldForm.stringType) {
                i.stringType = this.fieldForm.stringType;
              } else if (this.fieldForm.dateType) {
                i.dateType = this.fieldForm.dateType;
              } else if (this.fieldForm.numberType) {
                i.numberType = this.fieldForm.numberType;
              }
            }
          });
        }
      });
    },
    // union
    addSubDataSet() {
      const index = this.unionList.length;
      this.unionList.push({
        index: index,
        tableList: [],
        lineList: [],
        fieldList: [],
      });
    },
    deleteSubDataSet(index) {
      this.unionList.splice(index, 1);
    },
    changeSubDataSet(index) {
      this.fieldForm = {
        tableTitle: "",
        label: "",
        name: "",
        dimension: "",
        fieldType: "",
      };
      this.activeId = "";
      this.activeRow = {};
      this.currentIndex = index;
    },
  },
};
</script>
<style>
