  width: 100%;
}
.j-builder .table-wrap {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  width: 100%;
  min-height: 100%;
  padding: 100px 20px 60px;
}
.j-builder .table-wrap > .table,
.j-builder .table-wrap > .item {
  flex: 0 0 300px;
  position: relative;
  width: 300px;
  margin: 0 10px;
}
.j-builder .table-wrap > .table .el-card__header {
  cursor: move;
}
.j-builder .table-wrap > .table .el-card__header:hover {
  background-color: #fafafa;
}
.j-builder .table-wrap > .item {
  box-sizing: border-box;
  height: 300px;
  padding: 18px 20px;
  border: 1px dotted #409eff;
}
