  border-right: 1px solid #999;
}
.j-builder .line .el-dropdown {
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 2;
}
.j-builder .line .el-dropdown i {
  font-size: 16px;
}
.j-builder .line .el-dropdown i.active {
  color: #409eff;
}
.el-table .active-row > td {
  background: rgba(64, 158, 255, 0.2) !important;
}
.j-builder .pannel {
  float: left;
  overflow: auto;
  width: 210px;
  height: 100%;
  padding: 15px;
  background-color: #fff;
  border-left: 1px solid #ddd;
}
.j-builder .pannel .item {
  width: 100%;
}
.j-builder .footer {
  background-color: #fff;
  border-top: 1px solid #ddd;
}
/* union */
.j-union {
  float: left;
  width: 149px;
  height: 100%;
  padding: 20px;
  overflow: auto;
  background-color: #fafafa;
  border-left: 1px solid #ddd;
}
.j-union .union-list {
  position: relative;
}
.j-union .union-list > i {
  position: absolute;
