.j-union .union-list > i:nth-child(2) {
  display: none;
}
.j-union .union-list::after {
  position: absolute;
  left: 110px;
  top: 30px;
  bottom: 30px;
  width: 1px;
  background-color: #999;
  content: "";
}
.j-union .item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
