<template>
  <el-dialog
    title="关联设置"
    :visible.sync="settingDialog"
    width="400px"
    :before-close="closeSetting"
    :close-on-click-modal="false"
  >
    <el-form :model="form" size="medium" label-position="top">
      <el-form-item label="关联类型">
        <el-select v-model="form.type" class="w-100" clearable>
          <el-option
            v-for="item in joinOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="数据源1">
        <el-select v-model="form.table1" class="w-100" filterable clearable>
          <el-option-group
            v-for="(group, index) in startOptions"
            :key="index"
            :label="group.label"
          >
            <el-option
              v-for="item in group.fields"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-option-group>
        </el-select>
      </el-form-item>
      <el-form-item label="比较">
        <el-select v-model="form.compare" class="w-100" clearable>
          <el-option
            v-for="item in compareOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="数据源2">
        <el-select v-model="form.table2" class="w-100" filterable clearable>
          <el-option-group
            v-for="(group, index) in endOptions"
            :key="index"
            :label="group.label"
          >
            <el-option
              v-for="item in group.fields"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-option-group>
        </el-select>
      </el-form-item>
    </el-form>
    <span slot="footer">
      <el-button size="medium" @click="closeSetting">取 消</el-button>
      <el-button type="primary" size="medium" @click="saveSetting"
        >保 存</el-button
      >
    </span>
  </el-dialog>
</template>
<script>
const form = {
  type: "",
  table1: [],
  compare: "",
  table2: [],
};
export default {
  name: "JSetting",
  props: {
    startOptions: {
      type: Array,
      default: () => [],
    },
    endOptions: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      settingDialog: false,
      lineId: "",
      form: {
        ...form,
      },
      joinOptions: [
        { label: "左关联", value: "1" },
        { label: "右关联", value: "2" },
        { label: "内关联", value: "3" },
        { label: "全关联", value: "4" },
        { label: "笛卡尔积", value: "5" },
      ],
      compareOptions: [
        { label: "大于", value: "1" },
        { label: "小于", value: "2" },
        { label: "等于", value: "3" },
      ],
    };
  },
  methods: {
    init(id, data) {
      this.lineId = id;
      if (data && Object.keys(data).length) {
        this.form = data;
      }
      this.settingDialog = true;
    },
    closeSetting() {
      this.form = { ...form };
      this.settingDialog = false;
    },
    saveSetting() {
      this.$emit("save", this.lineId, this.form);
      this.closeSetting();
    },
  },
};
</script>
