<script>
import ECharts from "vue-echarts";
// import colors from "@/utils/pwcColors";
import CountTo from "vue-count-to";
import numeral from "numeral";

const factor = {
  grossCV: 0.3,
  co2e: 0.5,
  cost: 0.1,
};

export default {
  name: "EnergyPortfolioSimulation",
  components: {
    chart: ECharts,
    CountTo,
  },
  data() {
    return {
      searchForm: {
        benchmarkYear: "",
        planningYear: "",
        organisation: "",
      },
      totalList: [
        {
          name: "Gross CV",
          id: "grossCV",
          unit: "TJ",
          chartOption: {},
          benchmark: 0,
          planning: 0,
        },
        {
          name: "CO2e",
          id: "co2e",
          unit: "Tonne",
          chartOption: {},
          benchmark: 0,
          planning: 0,
        },
        {
          name: "Cost",
          id: "cost",
          unit: "USD'000",
          chartOption: {},
          benchmark: 0,
          planning: 0,
        },
      ],
      energyList: [
        {
          name: "Solar Electricity",
          id: "1",
          chartOne: {},
          chartTwo: {},
          chartThree: {},
          benchmark: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 0,
            price: 1,
          },
          planning: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 130,
            price: 1,
          },
          show: true,
        },
        {
          name: "Wind Electricity",
          id: "2",
          chartOne: {},
          chartTwo: {},
          chartThree: {},
          benchmark: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 240,
            price: 1,
          },
          planning: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 240,
            price: 1,
          },
          show: true,
        },
        {
          name: "Electricity",
          id: "3",
          chartOne: {},
          chartTwo: {},
          chartThree: {},
          benchmark: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 120,
            price: 1,
          },
          planning: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 150,
            price: 1,
          },
          show: true,
        },
        {
          name: "Petrol",
          id: "4",
          chartOne: {},
          chartTwo: {},
          chartThree: {},
          benchmark: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 500,
            price: 1,
          },
          planning: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 450,
            price: 1,
          },
          show: true,
        },
        {
          name: "Diesel",
          id: "5",
          chartOne: {},
          chartTwo: {},
          chartThree: {},
          benchmark: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 90,
            price: 1,
          },
          planning: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 70,
            price: 1,
          },
          show: true,
        },
        {
          name: "Waste Oils",
          id: "6",
          chartOne: {},
          chartTwo: {},
          chartThree: {},
          benchmark: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 10,
            price: 1,
          },
          planning: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 200,
            price: 1,
          },
          show: true,
        },
        {
          name: "Bioethanol",
          id: "7",
          chartOne: {},
          chartTwo: {},
          chartThree: {},
          benchmark: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 100,
            price: 1,
          },
          planning: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 100,
            price: 1,
          },
        },
        {
          name: "Bio Petrol",
          id: "8",
          chartOne: {},
          chartTwo: {},
          chartThree: {},
          benchmark: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 500,
            price: 1,
          },
          planning: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 250,
            price: 1,
          },
        },
        {
          name: "LPG",
          id: "9",
          chartOne: {},
          chartTwo: {},
          chartThree: {},
          benchmark: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 1000,
            price: 1,
          },
          planning: {
            grossCV: 0,
            co2e: 0,
            cost: 0,
            value: 0,
            price: 1,
          },
        },
      ],
      selectEnergy: "",
    };
  },
  computed: {
    treeSelectObj() {
      return {
        id: "id",
        label: "nameCode",
        children: "children",
        path: "path", // 路径
        content: "content", // 描述
        pid: "pid", // 父id
      };
    },
  },
  methods: {
    // 转换千位符和小数位数
    numeral,
    async init() {
      const year = new Date().getFullYear();
      this.searchForm.benchmarkYear = String(year - 1);
      this.searchForm.planningYear = String(year);
      this.energyList.forEach((item) => {
        this.initEnergyData(item);
      });
      this.initTotalListData();
    },
    changeEnergyData(item) {
      this.initEnergyData(item);
      this.initTotalListData();
    },
    initEnergyData(item) {
      item.benchmark.grossCV = item.benchmark.value * factor.grossCV;
      item.benchmark.co2e = item.benchmark.value * factor.co2e;
      item.benchmark.cost = item.benchmark.value * item.benchmark.price;
      item.planning.grossCV = item.planning.value * factor.grossCV;
      item.planning.co2e = item.planning.value * factor.co2e;
      item.planning.cost = item.planning.value * item.planning.price;
      const option1 = this.getChartOption(
        0,
        item.benchmark.grossCV,
        item.planning.grossCV
      );
      const option2 = this.getChartOption(
        1,
        item.benchmark.co2e,
        item.planning.co2e
      );
      const option3 = this.getChartOption(
        2,
        item.benchmark.cost,
        item.planning.cost
      );
      this.$set(item, "chartOne", option1);
      this.$set(item, "chartTwo", option2);
      this.$set(item, "chartThree", option3);
    },
    initTotalListData() {
      this.totalList.forEach((item, index) => {
        const benchmark = this.getTotalSum("benchmark", item.id);
        const planning = this.getTotalSum("planning", item.id);
        this.$set(item, "benchmark", benchmark);
        this.$set(item, "planning", planning);
        const option = this.getChartOption(index, benchmark, planning);
        this.$set(item, "chartOption", option);
      });
    },
    getTreeSelected(keys) {
      if (Array.isArray(keys)) {
        const tempDepartmentId = this.$Utils.copyData(this.departmentId);
        const tempKeys = this.$Utils.copyData(keys);
        if (tempDepartmentId.sort().toString() !== tempKeys.sort().toString()) {
          this.departmentId = keys;
        }
      } else {
        if (this.departmentId !== keys) {
          this.departmentId = keys;
        }
      }
    },
    getTotalSum(year, type) {
      let sum = 0;
      this.energyList.forEach((item) => {
        if (item.show) {
          sum += item[year][type];
        }
      });
      return sum;
    },
    getChartOption(index, benchmark, planning) {
      const option = {
        color: ["#eee", "#eee", "#7d7d7d", "orange"],
        angleAxis: {
          max: 100,
          splitLine: false,
          axisLine: false,
        },
        radiusAxis: {
          type: "category",
          data: [" "],
          axisLine: false,
        },
        polar: {
          radius: ["50%", "80%"],
        },
        series: [
          {
            type: "bar",
            data: [100],
            coordinateSystem: "polar",
            stack: "benchmark",
          },
          {
            type: "bar",
            data: [100],
            coordinateSystem: "polar",
            stack: "planning",
          },
          {
            type: "bar",
            data: [0],
            coordinateSystem: "polar",
            name: "benchmark",
            stack: "benchmark",
            roundCap: true,
          },
          {
            type: "bar",
            data: [0],
            coordinateSystem: "polar",
            name: "planning",
            stack: "planning",
            roundCap: true,
          },
        ],
      };
      if (benchmark && planning) {
        if (benchmark > planning) {
          option.series[2].data[0] = 100;
          option.series[3].data[0] = (planning / benchmark) * 100;
        } else {
          option.series[3].data[0] = 100;
          option.series[2].data[0] = (benchmark / planning) * 100;
        }
      } else if (benchmark && !planning) {
        option.series[2].data[0] = 100;
        option.series[3].data[0] = 0;
      } else if (!benchmark && planning) {
        option.series[2].data[0] = 0;
        option.series[3].data[0] = 100;
      } else {
        option.series[2].data[0] = 0;
        option.series[3].data[0] = 0;
      }
      return option;
    },
    getChartPercentage(benchmark, planning) {
      let num = 0;
      if (!planning) {
        return num;
      }
      if (benchmark) {
        num = (planning / benchmark) * 100;
      }
      return num;
    },
    calcMaxValue(num1, num2) {
      const maxValue = 1.05;
      let value = 105;
      if (num1 >= num2 && num1 >= 100) {
        value = num1 * maxValue;
      } else if (num2 > num1 && num2 >= 100) {
        value = num2 * maxValue;
      }
      return value;
    },
    calcPercentage(current, compare) {
      let value = 0;
      if (current) {
        const maxValue = this.calcMaxValue(current, compare);
        value = (current / maxValue) * 100;
      }
      return value;
    },
    formatTooltip(item) {
      let text = "0%";
      if (item.planning.value) {
        if (item.benchmark.value) {
          const num = (item.planning.value / item.benchmark.value) * 100;
          text = (num.toFixed(2) * 100) / 100 + "%";
        } else {
          text = "N/A";
        }
      }
      return text;
    },
    addEnergy() {
      this.energyList.forEach((item) => {
        if (item.id === this.selectEnergy) {
          this.$set(item, "show", true);
          this.selectEnergy = "";
        }
      });
      this.initTotalListData();
    },
    removeEnergy(item) {
      item.show = false;
      this.initTotalListData();
    },
  },
  created() {
    this.init();
  },
};
</script>
