import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";
import Projects from "../views/Projects/index.vue";
import JoinTables from "../views/JoinTables/index.vue";
import Simulation from "../views/EnergySimulation/index.vue";
import SermonCalendar from "../views/SermonCalendar/index.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },

  {
    path: "/projects",
    name: "projects",
    component: Projects,
  },
  {
    path: "/join-tables",
    name: "joinTables",
    component: JoinTables,
  },
  {
    path: "/simulation",
    name: "simulation",
    component: Simulation,
  },
  {
    path: "/sermon-calendar",
    name: "sermonCalendar",
    component: SermonCalendar,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
